import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Table, {
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from 'material-ui/Table';
import Paper from 'material-ui/Paper';
import getState from '../index';
import { compose, lifecycle, mapProps } from 'recompose';
import { Wallet, web3 } from '../lib/contracts';

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
});

let id = 0;
function createData(name, calories, fat, carbs, protein) {
  id += 1;
  return { id, name, calories, fat, carbs, protein };
}

const data = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
  createData('Eclair', 262, 16.0, 24, 6.0),
  createData('Cupcake', 305, 3.7, 67, 4.3),
  createData('Gingerbread', 356, 16.0, 49, 3.9),
];
const SimpleTableUI = withStyles(styles)(({ contractStates, classes }) => (
  <Paper className={classes.root}>
    <Table className={classes.table}>
      <TableHead>
        <TableRow>
          <TableCell>Name</TableCell>
          <TableCell numeric>Type</TableCell>
          <TableCell numeric>Value</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {/* {console.log(getState())} */}
        {contractStates &&
          contractStates.map(n => {
            // <TableRow key={n.id}>
            //   <TableCell>{n.name}</TableCell>
            //   <TableCell numeric>{n.calories}</TableCell>
            //   <TableCell numeric>{n.fat}</TableCell>
            // </TableRow>
            console.log('n.state', n.state.length);
            return n.state.map(e => {
              console.log(e);
              return (
                <div>
                  <div> {n.contractName} </div>
                  <div> {e.name} </div>
                  <div> {e.value} </div>
                </div>
              );
            });
          })}
      </TableBody>
    </Table>
  </Paper>
));

SimpleTableUI.propTypes = {
  classes: PropTypes.object.isRequired,
};

const SimpleTable = compose(
  lifecycle({
    async componentWillMount() {
      const contractState = await Wallet.getContractState();
      await this.setState({
        contractStates: contractState,
      });
      console.log('mounted table', this.state.contractStates);
      await this.forceUpdate();

      // web3.eth.getBlock(this.props.log.blockNumber, (err, block) => {
      //   this.setState({
      //     block,
      //   });
      // });
    },
  }),
  mapProps(({ contractStates }) => {
    console.log('mapping props', contractStates);
    return {
      // date: block ? moment(new Date(block.timestamp * 1000)).format('LL') : null,
      // numCrcs: Web3.fromWei(log.args.value).toString(),
      contractStates,
    };
  })
)(SimpleTableUI);

export default SimpleTable;



